"use client"

import { useState } from "react"
import Image from "next/image"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Select } from "@/components/ui/select"

const products = [
  {
    id: 1,
    title: "Вінтажна шкіряна куртка",
    price: "2500 грн",
    category: "Верхній одяг",
    era: "1980-ті",
    condition: "Відмінний",
    image: "https://images.pexels.com/photos/1124468/pexels-photo-1124468.jpeg",
  },
  {
    id: 2,
    title: "Класична сукня 60-х",
    price: "1800 грн",
    category: "Сукні",
    era: "1960-ті",
    condition: "Добрий",
    image: "https://images.pexels.com/photos/1375736/pexels-photo-1375736.jpeg",
  },
  {
    id: 3,
    title: "Ретро джинси",
    price: "1200 грн",
    category: "Джинси",
    era: "1970-ті",
    condition: "Відмінний",
    image: "https://images.pexels.com/photos/1082529/pexels-photo-1082529.jpeg",
  },
  {
    id: 4,
    title: "Вінтажна блуза",
    price: "900 грн",
    category: "Блузи",
    era: "1950-ті",
    condition: "Добрий",
    image: "https://images.pexels.com/photos/6626903/pexels-photo-6626903.jpeg",
  },
  {
    id: 5,
    title: "Шкіряні черевики",
    price: "1600 грн",
    category: "Взуття",
    era: "1990-ті",
    condition: "Відмінний",
    image: "https://images.pexels.com/photos/1598505/pexels-photo-1598505.jpeg",
  },
  {
    id: 6,
    title: "Твідовий піджак",
    price: "2200 грн",
    category: "Верхній одяг",
    era: "1960-ті",
    condition: "Добрий",
    image: "https://images.pexels.com/photos/6626967/pexels-photo-6626967.jpeg",
  },
]

export default function ShopPage() {
  const [sortBy, setSortBy] = useState("newest")
  const [filterCategory, setFilterCategory] = useState("all")
  const [filterEra, setFilterEra] = useState("all")
  const [filterCondition, setFilterCondition] = useState("all")

  const categories = ["Всі", "Верхній одяг", "Сукні", "Джинси", "Блузи", "Взуття"]
  const eras = ["Всі", "1950-ті", "1960-ті", "1970-ті", "1980-ті", "1990-ті"]
  const conditions = ["Всі", "Відмінний", "Добрий"]

  const filteredProducts = products.filter(product => {
    if (filterCategory !== "all" && product.category !== filterCategory) return false
    if (filterEra !== "all" && product.era !== filterEra) return false
    if (filterCondition !== "all" && product.condition !== filterCondition) return false
    return true
  })

  return (
    <div className="py-24 px-4 bg-white">
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-playfair font-bold mb-4">Вінтажний одяг</h1>
          <p className="text-xl text-gray-600">Знайдіть свій унікальний стиль</p>
        </div>

        {/* Filters */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
          <select 
            className="border border-gray-300 rounded-md p-2"
            value={filterCategory}
            onChange={(e) => setFilterCategory(e.target.value)}
          >
            {categories.map((category) => (
              <option key={category} value={category === "Всі" ? "all" : category}>
                {category}
              </option>
            ))}
          </select>

          <select
            className="border border-gray-300 rounded-md p-2"
            value={filterEra}
            onChange={(e) => setFilterEra(e.target.value)}
          >
            {eras.map((era) => (
              <option key={era} value={era === "Всі" ? "all" : era}>
                {era}
              </option>
            ))}
          </select>

          <select
            className="border border-gray-300 rounded-md p-2"
            value={filterCondition}
            onChange={(e) => setFilterCondition(e.target.value)}
          >
            {conditions.map((condition) => (
              <option key={condition} value={condition === "Всі" ? "all" : condition}>
                {condition}
              </option>
            ))}
          </select>

          <select
            className="border border-gray-300 rounded-md p-2"
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
          >
            <option value="newest">Найновіші</option>
            <option value="price-low">Ціна: від низької до високої</option>
            <option value="price-high">Ціна: від високої до низької</option>
          </select>
        </div>

        {/* Products Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredProducts.map((product) => (
            <Card key={product.id} className="overflow-hidden group">
              <div className="relative h-96">
                <Image
                  src={product.image}
                  alt={product.title}
                  fill
                  sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                  className="object-cover group-hover:scale-105 transition-transform duration-300"
                />
              </div>
              <div className="p-6">
                <h3 className="text-xl font-semibold mb-2">{product.title}</h3>
                <p className="text-lg font-medium text-gray-900 mb-2">{product.price}</p>
                <div className="flex flex-wrap gap-2 mb-4">
                  <span className="text-sm bg-gray-100 px-2 py-1 rounded">{product.category}</span>
                  <span className="text-sm bg-gray-100 px-2 py-1 rounded">{product.era}</span>
                  <span className="text-sm bg-gray-100 px-2 py-1 rounded">{product.condition}</span>
                </div>
                <Link href={`/product/${product.id}`}>
                  <Button className="w-full bg-black text-white hover:bg-gray-800">
                    Детальніше
                  </Button>
                </Link>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
